import apiCore from "@/utils/apiCore";


const HTTP = new apiCore({
    baseURL: process.env.VUE_APP_URL,
    headers: {
        'Content-Type': 'application/json;charset=utf-8',
    },
});

export function getDictData (dictCodes:any) {
    return HTTP.get('/sys/dict/data?dictCodes=' + dictCodes, {});
}

export function invalidDictHash(map:any) {
    return HTTP.post('/sys/dict/invalid-hash', map);
}


export function getPage(query:any) {
    return HTTP.get('/sys/dict/page', query);
}

export function addObj(obj:any) {
    return HTTP.post('/sys/dict', obj);
}

export function getObj(id:any) {
    return HTTP.get('/sys/dict/' + id, {});
}

export function delObj(id:any) {
    return HTTP.delete('/sys/dict/' + id, {});
}

export function putObj(obj:any) {
    return HTTP.post('/sys/dict/updateInfo', obj);
}
