import apiCore from "@/utils/apiCore";
import {apiUrl} from "@/api/manage/apiUrl";


const HTTP = new apiCore({
    baseURL: process.env.VUE_APP_URL,
    headers: {
        'Content-Type': 'application/json;charset=utf-8',
    },
});

export function getLoginUserRouter () {
    return HTTP.get('/syspermission/router', {});
}

export function getList () {
    return HTTP.get('/syspermission/list', {});
}

export function addObj(obj:any) {
    return HTTP.post('/syspermission', obj);

}

export function putObj(obj:any) {
    return HTTP.post('/syspermission/updateInfo', obj);
}

export function delObj(id:any) {
    return HTTP.delete('/syspermission/' + id, {});
}
