import axios from 'axios'
import Vue from 'vue'
// tslint:disable-next-line:class-name
import { ACCESS_TOKEN } from '@/store/mutationTypes'
import Message from 'element-ui'
// @ts-ignore
const that = this
class ApiCore {
  public config: any
  public instance: any
  public token: string =
    'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJhdWQiOiJ7T3JnSWRBbGw6JzExLDEyJyxPcmdJZDonMTInLE9yZ1R5cGU6J00nLE9yZ1R5cGVfQzonTScsT3JnSWRfQzonMTInLE9yZ0lkTTonMTInLEVtcElkOiczODAxOCcsU3RvcmVUeXBlOidIUScsU3RvcmVJZDonMTMnLFN0b3JlSWRBbGw6JzkxLDkyLDkzJ30ifQ.K2PqyiFcq4i1QJFWsPRL6VYpfR-9b-GZVzypLrUqBEM'
  // public token:string = "eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJhdWQiOiJ7T3JnSWRBbGw6JzExLDEyJyxPcmdJZDonMTInLE9yZ1R5cGU6J00nLE9yZ1R5cGVfQzonTScsT3JnSWRfQzonMTInLE9yZ0lkTTonMTInLEVtcElkOiczODAxOCcsU3RvcmVUeXBlOidTUicsU3RvcmVJZDonOTInLFN0b3JlSWRBbGw6JzkyJ30ifQ.kjBiOFpza5b5wKmPLE4drn6_5-t-YMrc3m8kKQoXjo8";



  constructor(config: any) {
    this.config = config
    this.create()
  }
  public create() {
    this.config.transformRequest = [
      (data: any, headers: any): string => {
        return JSON.stringify(data)
      },
    ]
    this.instance = axios.create(this.config)
    // tslint:disable-next-line:no-trailing-whitespace

    this.instance.interceptors.request.use(
      (config: any) => {
        // if (window.localStorage.userInfo) {
        //   config.headers.common.Authorization = JSON.parse(window.localStorage.userInfo).token;
        // }
        // config.headers.common.token =this.token;
        // 在发送请求之前做些什么
        const token = Vue.ls.get(ACCESS_TOKEN)
        if (token) {
          config.headers['Authorization'] = 'Bearer ' + token // 让每个请求携带自定义 token 请根据实际情况自行修改
        }
        return config
      },
      (error: any) => {
        // 对请求错误做些什么
        return Promise.reject(error)
      }
    )

    this.instance.interceptors.response.use(
      (response: any) => {
        // Do something with response data
        return response.data
      },
      (error: any) => {
        // 对响应错误做点什么
        const status: any = error.response.status
        if (status === 403) {
          window.location.href = '/login.html/#/user/login'
          return
        }
      }
    )
  }
  // 发送请求
  public async send(config: any) {
    try {
      const res = await this.instance.request(config)
      if (res.code === 200 || res.access_token || res.records) {
        return Promise.resolve(res)
      } else {
        // @ts-ignore
        Message.Message({
          type: 'error',
          message: res.msg || '网络错误',
        })
      }
    } catch (e) {
      // @ts-ignore
      Message.Message({
        type: 'error',
        message: e.msg || e.message,
      })
    }
    // return this.instance.request(config);
  }
  public get(url: string, params: object = {}, responseType?: string) {
    return this.send({
      url,
      method: 'GET',
      params,
      responseType: responseType || '',
    })
  }
  public post(url: string, data: object = {}) {
    return this.send({
      url,
      method: 'POST',
      data,
    })
  }
  public post1(url: string, params: object = {}) {
    return this.send({
      url,
      method: 'POST',
      params,
    })
  }
  public delete(url: string, params: object = {}) {
    return this.send({
      url,
      method: 'DELETE',
      params,
    })
  }
  public put(url: string, data: object = {}) {
    return this.send({
      url,
      method: 'PUT',
      data,
    })
  }
}

export default ApiCore
