import Vue from "vue";
import Vuex from "vuex";
import {ConstantRouters} from "@/router/constantRouters";
import {generatorDynamicRouter} from '@/router/dynamicRouter'
import getters from './getters'
import {login, logout} from '@/api/Auth'
import {getDictData,invalidDictHash} from "@/api/manage/sys/sysDictApi";

import {
    ACCESS_TOKEN,
    USER_INFO,
    PERMISSIONS,
    ROLES,
    ORG_INFO,
    SET_DICT_CODE,
    DELETE_INVALID_DICT, SET_DICT_REQUEST_CACHE, RESET_DICT_REQUEST_CACHE_ITEM
} from '@/store/mutationTypes'

Vue.use(Vuex)

// 字典项hash列表的Key
const DICT_HASH_KEY = 'dict-hashes';
const DICT_DATA_KEY_PREFIX = 'dict-data-';
// 失效时间
const DICT_TTL = 7 * 24 * 60 * 60 * 1000;

export default new Vuex.Store({
    state: {
        storeType: sessionStorage.getItem('storeType'),
        setting: sessionStorage.getItem('setting'),
        filterParam: sessionStorage.getItem('filterParam'),
        optionData: sessionStorage.getItem('optionData'),
        routers: ConstantRouters,
        userRouters: [],
        token: sessionStorage.getItem('token'),
        roles: sessionStorage.getItem('roles'),
        permissions: sessionStorage.getItem('permissions'),
        info: sessionStorage.getItem('info'),
        org: sessionStorage.getItem('org'),
        dictDataCache: [],
        dictRequestCache: {}
    },
    mutations: {
        [SET_DICT_CODE](state, { dictCode, dictList }) {
            // state.dictDataCache=[...state.dictDataCache,{`${dictCode}`:dictList}];

            Vue.set(state.dictDataCache, dictCode, dictList)
            console.log(state.dictDataCache);
        },
        [DELETE_INVALID_DICT](state, dictCode) {
            Vue.delete(state.dictDataCache, dictCode)
        },
        [SET_DICT_REQUEST_CACHE](state, dictCode) {
            state.dictRequestCache[dictCode] = true;
        },
        [RESET_DICT_REQUEST_CACHE_ITEM](state, dictCode) {
            state.dictRequestCache[dictCode] = false;
        },
        SET_STORETYPE(state, val) {
            sessionStorage.setItem('storeType', val)
            state.storeType = val;
        },
        SET_OPTION_DATA(state, val) {
            sessionStorage.setItem('optionData', val)
            state.optionData = val;
        },
        SET_SETTING(state, val) {
            sessionStorage.setItem('setting', val)
            state.setting = val;
        },
        SET_FILTER_PARAM(state, val) {
            sessionStorage.setItem('filterParam', val)
            state.filterParam = val;
        },
        SET_ROUTERS(state, val) {
            state.userRouters = val
            state.routers = val
        },
        SET_TOKEN(state, val) {
            sessionStorage.setItem('token', val)
            state.token = val
        },
        SET_INFO(state, val) {
            sessionStorage.setItem('info', val)
            state.info = val
        },
        SET_ORG(state, val) {
            sessionStorage.setItem('org', val)
            state.org = val
        },
        SET_ROLES(state, val) {
            sessionStorage.setItem('roles', val)
            state.roles = val
        },
        SET_PERMISSIONS(state, val) {
            sessionStorage.setItem('permissions', val)
            state.permissions = val
        }
    },
    actions: {
        Login({commit}: any, loginParam: any) {
            return new Promise((resolve, reject) => {
                // loginParam.username = "tt3";
                // loginParam.password = "tt3";
                loginParam.username = "admin";
                loginParam.password = "a123456";
                // loginParam.password = encryption(loginParam.password)

                loginParam.client_id = "actuator";
                loginParam.client_secret = "123456";
                loginParam.scope = "server";
                login(loginParam).then((res: any) => {
                    console.log('res', res)
                    // TODO token刷新机制
                    const ttl = 7 * 24 * 60 * 60 * 1000
                    const accessToken = res.access_token
                    // const refreshToken = res.refresh_token

                    // if (res.permissions.length < 0) {
                    //     reject(new Error('getInfo: roles must be a non-null array !'))
                    // }
                    Vue.ls.set(ORG_INFO, res.organization, ttl)
                    commit('SET_INFO', res.info)
                    Vue.ls.set(USER_INFO, res.info, ttl)
                    commit('SET_INFO', res.info)
                    Vue.ls.set(ROLES, res.roles, ttl)
                    commit('SET_ROLES', res.roles)
                    Vue.ls.set(PERMISSIONS, res.permissions, ttl)
                    commit('SET_PERMISSIONS', res.permissions)
                    Vue.ls.set(ACCESS_TOKEN, accessToken, ttl)
                    commit('SET_TOKEN', accessToken)

                    Vue.ls.set("activeMenuIndex",0);

                    resolve(res)
                }).catch((error: any) => {
                    reject(error)
                })
            })
        },
        // 登出
        Logout ({ dispatch, commit, state }: any) {
            return new Promise((resolve:any) => {
                logout().then(() => {
                    resolve()
                }).catch(() => {
                    resolve()
                }).finally(() => {
                    dispatch('CLEAN_USER_INFO').then(() => {
                        setTimeout(() => {
                            window.location.reload()
                        }, 10)
                    })
                })
            })
        },

        CLEAN_USER_INFO ({ commit, state }: any) {
            return new Promise((resolve) => {
                commit('SET_TOKEN', '')
                commit('SET_ROLES', [])
                Vue.ls.remove(ACCESS_TOKEN)
                Vue.ls.remove(USER_INFO)
                Vue.ls.remove(PERMISSIONS)
                Vue.ls.remove(ROLES)
                resolve()
            })
        },

        GenerateRoutes({commit}: any) {
            return new Promise((resolve: any, reject:any) => {
                generatorDynamicRouter().then(routers => {
                    commit('SET_ROUTERS', routers)
                    resolve()
                }).catch((err: any) => {
                    reject(err);
                })
            })
        },
        // 获取字典数据
        async getDictData({ commit, state }:any, dictCode:any = []) {
            try {

                // const noDataList = dictCode.filter((v:any) => {
                //     console.log(state.dictDataCache[v]);
                //     if (!state.dictDataCache[v]) {
                //         const lsVal = Vue.ls.get(DICT_DATA_KEY_PREFIX + v);
                //         if (lsVal) {
                //             // state.dictDataCache[v] = lsVal;
                //             commit(SET_DICT_CODE, {
                //                 dictCode: v,
                //                 dictList: lsVal
                //             });
                //         } else {
                //             return v;
                //         }
                //     }
                //     else
                //     {
                //         return  v;
                //     }
                // });
                // console.log(noDataList);
                // if (noDataList.length === 0 || state.dictRequestCache[noDataList.join(',')]) {
                //     return;
                // }
                // commit(SET_DICT_REQUEST_CACHE, noDataList.join(','));

                const res = await getDictData(dictCode);
                if (res.code === 200 && res.data) {
                    res.data.forEach((dict:any) => {
                        const dictCode:string = dict.dictCode;
                        const hashCode:string = dict.hashCode;
                        const dictList:any = [];
                        dict.dictItems.forEach((item:any) => {
                            // 存储数据类型
                            dictList.push({ ...item, valueType: dict.valueType });
                        })
                        commit(SET_DICT_CODE, {
                            dictCode,
                            dictList
                        });
                        Vue.ls.set(DICT_DATA_KEY_PREFIX + dictCode, dictList, DICT_TTL);
                        Vue.ls.set(dictCode,dictList);
                        // 存储字典项Hash
                        let hashes = Vue.ls.get(DICT_HASH_KEY);
                        let map = hashes ? JSON.parse(hashes) : {};
                        map[dictCode] = hashCode;
                        Vue.ls.set(DICT_HASH_KEY, JSON.stringify(map));
                    })
                }
                // commit(RESET_DICT_REQUEST_CACHE_ITEM, noDataList.join(','));
            } catch (e) {
                console.log(e);
            }
        },
        async checkDictStatus({ commit, state }) {
            // 获取ls中的Hash表
            const hashes = Vue.ls.get(DICT_HASH_KEY);
            const map = hashes ? JSON.parse(hashes) : null;
            // 如果有属性，则去后台查询是否过期
            if (map && Object.getOwnPropertyNames(map).length > 0) {
                const res = await invalidDictHash(map);
                if (res.code === 200) {
                    // 删除对应过期数据
                    for (let dictCode of res.data) {
                        Vue.ls.remove(DICT_DATA_KEY_PREFIX + dictCode);
                        delete map[dictCode];
                        // delete this.dictDataCache[dictCode]
                        commit(DELETE_INVALID_DICT, dictCode);
                    }
                    // 更新删除数据后的Hash表
                    Vue.ls.set(DICT_HASH_KEY, JSON.stringify(map));
                }
            }
        }
    },
    getters
})

