@Component({
  components: {},
})
export default class TopBar extends Vue {
  get userInfo() {
    console.log("Vue.ls.get('sayhi__USER_INFO')")
    const userInfo = sessionStorage.getItem('sayhi__USER_INFO')
    try {
      if (userInfo) {
        const info = JSON.parse(userInfo)
        return info.value
      }
    } catch (e) {
      location.href = '/login.html/#/user/login'
    }
  }
  public loginout() {
    localStorage.clear()
    location.href = '/login.html/#/user/login'
  }
}
