




















































































import { Component, Vue } from 'vue-property-decorator'
@Component({
  components: {}
})
export default class Aside extends Vue {
  menus: any[] = []
  activeIndex: number = 0
  childrenMenu: any = []

  mounted() {
    const menuData = this.$store.state.userRouters.find(
      (item: any) => item.path === '/'
    ).children
    menuData.forEach((rows: any) => {
      this.menus.push(rows)
    })
    Vue.ls.set('menus', this.menus)
    console.log('route', this.$route)
  }
  get active() {
    let index = -1
    for (let i = 0; i < this.menus.length; i++) {
      if (!this.menus[i].children) {
        continue
      } else {
        index = this.menus[i].children.findIndex((target: any) => {
          return target.path === this.$route.path
        })
        if (index !== -1) {
          console.log('index', index)
          // this.handleSelect(i, '')
          return i + ''
        }
      }
      // if (i === this.menus.length - 1) {
      //   this.handleSelect(0, '')
      // }
    }
    return 0 + ''
  }
  // setChildrenMenu(key: number) {
  //   this.activeIndex = key;
  //   Vue.ls.set('activeMenuIndex', this.activeIndex);
  //   this.childrenMenu = this.menus[key];
  //   Vue.ls.set('childrenMenu', this.childrenMenu);
  //   this.$emit('setMenu', this.childrenMenu);
  // }

  private handleSelect(key: any, keyPath: any) {
    console.log('key', key, 'path', keyPath)
    // Vue.ls.set('activeMenuIndex', this.activeIndex);
    this.childrenMenu = this.menus[key]
    Vue.ls.set('childrenMenu', this.childrenMenu)
    this.$emit('setMenu', this.childrenMenu)

    if (this.menus.length > key && this.menus[key].children) {
      if (this.menus[key].children.length > 0) {
        console.log(
          'this.menus.children[0].path',
          this.menus[key].children[0].path
        )
        try {
          this.$router.push({
            path: this.menus[key].children[0].path
          })
        } catch (e) {}
      }
    }
  }
}
