



































































import { Component, Prop, Vue } from 'vue-property-decorator'
import TopBar from '@/components/TopBar.vue'

@Component({
  components: {
    TopBar
  }
})
export default class Header extends Vue {
  // @Prop() dataSource!: any
  // private dataSource!: any
  activeItemIndex: number = Vue.ls.get('activeItemIndex') || -1
  childrenMenuShow: boolean = false

  get dataSource() {
    let menus = Vue.ls.get('menus')
    if (!menus) {
      // return {meta: {title: ''}}
      menus = []
      const menuData = this.$store.state.userRouters.find(
        (item: any) => item.path === '/'
      ).children
      menuData.forEach((rows: any) => {
        menus.push(rows)
      })
    }
    for (let i = 0; i < menus.length; i++) {
      console.log('menus[i].children', menus[i].children)
      if (!menus[i].children) {
        continue
      } else {
        const index = menus[i].children.findIndex((target: any) => {
          return target.path === this.$route.path
        })
        if (index !== -1) {
          // this.handleSelect(i, '')
          return menus[i]
        }
      }
    }
    return { meta: { title: '' } }
  }

  private childList: any = {
    value: {
      meta: '',
      children: []
    }
  }

  mounted() {}

  menuClick(item: any, index: number) {
    this.activeItemIndex = index
    this.childrenMenuShow = true
    Vue.ls.set('activeItemIndex', this.activeItemIndex)
    // console.log(item.path)
    if (item.path) {
      window.location.href = '/index.html/#' + item.path
      Vue.ls.set(item.path, {})
    }
  }

  childrenMenuClick(item: any) {
    this.$router.push({ path: item.path })
  }
}
