import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './core/Use'
import './permission'
// @ts-ignore
import echarts from 'echarts'
// 需要挂载到Vue原型上
Vue.prototype.$echarts = echarts

Vue.config.productionTip = false
// @ts-ignore
import VueAMap from 'vue-amap'
Vue.prototype.$VueAMap = VueAMap
Vue.use(VueAMap)

// 初始化vue-amap
VueAMap.initAMapApiLoader({
  // 高德key
  key: '195de24ce21b08cd8a554cf180195b2d',
  // 插件集合 （插件按需引入）
  plugin: [
    'AMap.Geolocation',
    'AMap.Scale',
    'AMap.OverView',
    'AMap.ToolBar',
    'AMap.MapType',
    'AMap.Geocoder',
  ],
  uiVersion: '1.0.11',
})
new Vue({
  router,
  render: (h) => h(App),
  store,
}).$mount('#app')
