import apiCore from '@/utils/apiCore';

const HTTP = new apiCore({
    baseURL: process.env.VUE_APP_URL,
    headers: {
        'Content-Type': 'application/json;charset=utf-8',
    },
});

export function login (parameter:object): any {
   return  HTTP.get(`/oauth/token`, Object.assign({'grant_type': 'password'}, parameter));
}

export function logout (): any {
    return  HTTP.delete(`/oauth/logout`, {});
}
